<template>
<nav class="cus-nav navbar navbar-fixed-top">
  <div class="container-fluid">
    <ul class="nav navbar-nav nav-pills pull-left">
    <!-- 首页展现 打开侧边栏 -->
      <li @click="toggleSidebar" v-if="$route.path === '/home' || $route.path === '/'" >
        <a href="javascript:;">
          <transition name="rotate">
            <i v-if="!isSidebarShow" class="fa fa-navicon"></i>
          </transition>
        </a>
      </li>
      <!-- 非首页 展现 回到首页效果 -->
      <li v-if="$route.path !== '/home' && $route.path !== '/'" >
        <router-link to="/">
          <i class="fa fa-arrow-left"></i>
        </router-link>
      </li>
      <!-- title不为空 则展现 若不为首页 则路由不指向首页 -->
      <li v-if="title !== ''">
        <router-link v-if="$route.path === '/'" class="home" to="/home">{{ title }}</router-link>
        <a v-else class="home">{{ title }}</a>
      </li>
    </ul>
    <!-- 首页 则 展现 -->
    <ul v-if="$route.path === '/home' || $route.path === '/'" class="nav navbar-nav nav-pills pull-right">
      <li>
        <router-link v-if="!isUserLogin" to="/login"><i class="fa fa-bell"></i></router-link>
        <router-link v-if="isUserLogin" to="/message"><i class="fa fa-bell"></i></router-link>
      </li>
      <li>
        <a @click.stop="toggleCtrlBox" href="javascript:;"><i class="fa fa-ellipsis-v"></i></a>
      </li>
    </ul>

    <!-- 文章页展现 -->
    <ul v-if="$route.name === 'article' || $route.name === 'share'" class="nav navbar-nav nav-pills pull-right">
      <li>
        <!-- <a href="javascript:;"><i class="fa fa-share-alt"></i></a> -->
        <router-link :to="{name: 'share'}"><i class="fa fa-share-alt"></i></router-link>
      </li>
      <li>
        <a @click="changeCollection" href="javascript:;"><i :class="{collect: curNews.isCollected}" class="fa fa-star"></i></a>
      </li>
      <li>
        <a href="javascript:;">
          <i class="fa fa-commenting"></i>
          <span>{{ curNews.comments }}</span>
        </a>
      </li>
      <li>
        <a @click="changeLikes" href="javascript:;">
          <i :class="{like: curNews.isLiked}" class="likes fa fa-thumbs-up"></i>
          <span>{{ curNews.likes }}</span>
        </a>
      </li>
    </ul>
  </div>

  <!-- 控制列表项 -->
  <ul v-if="isCtrlShow" class="cus-ctrl nav nav-tabs nav-stacked">
    <li @click.stop="toggleNightPattern"><a href="javascript:;">夜间模式</a></li>
    <li @click.stop="toggleCtrlBox">
      <router-link to="/setting">设置选项</router-link>
    </li>
  </ul>
</nav>
</template>

<script>

  import { mapState } from 'vuex'

  export default {
    name: 'NewsHeader',
    data () {
      return {
        title: '',
        id: ''
      }
    },
    computed: { // es6解构赋值语法
      ...mapState({
        curNews: state => state.curNews,
        collectList: state => state.collectList,
        columnTitle: state => state.columnTitle,
        isBoxShow: state => state.isBoxShow,
        isCtrlShow: state => state.isCtrlShow,
        isUserLogin: state => state.isUserLogin,
        isSidebarShow: state => state.isSidebarShow
      })
    },
    methods: {
      changeTitle () {
        switch (this.$route.path) {
          case '/':
            this.title = '首页'
            break
          case '/home':
            this.title = '首页'
            break
          case '/setting':
            this.title = '设置'
            break
          case '/collect':
            this.title = this.collectList.length > 0 ? this.collectList.length + '条收藏' : '无收藏'
            break
          case '/login':
            this.title = '登录'
            break
          case '/message':
            this.title = '消息'
            break
          case '/aboutme':
            this.title = '关于我'
            break
          default:
            this.title = ''
            break
        }
        this.title = this.$route.name === 'column' ? this.columnTitle : this.title
        localStorage.setItem('title', this.title)
      },
      changeLikes () {
        if (this.curNews.likes) { // 非空才可增加
          if (!this.curNews.isLiked) {
            this.curNews.likes += 1
            this.curNews.isLiked = true
            if (!this.isBoxShow) { // 防止短时间快速点击
              this.$store.dispatch('toggleLikedBox')
            }
          } else {
            this.curNews.likes -= 1
            this.curNews.isLiked = false
          }
        }
      },
      changeCollection () {
        if (!this.curNews.isCollected) {
          this.curNews.isCollected = true
          this.$store.dispatch('setCollectList')
        } else {
          this.curNews.isCollected = false
          this.$store.dispatch('reduceCollectList', this.id)
        }
      },
      toggleSidebar () {
        this.$store.dispatch('toggleSidebar')
      },
      toggleNightPattern () {
        this.$store.dispatch('toggleCtrlBox') // 关闭设置框
        this.$store.dispatch('toggleNightPattern')
      },
      toggleCtrlBox () {
        this.$store.dispatch('toggleCtrlBox')
      }
    },
    watch: {
      '$route': 'changeTitle',  // 可以用数组来监听路由变化执行多个函数
      'columnTitle': 'changeTitle' // 监听栏目标题变量 发现变化立即更换标题 否则跳转页面后无法及时更改栏目标题
    },
    created () {
      this.changeTitle() // 页面创建后先设置标题
      this.id = this.$route.params.id
      if (this.title === '') { // 保证刷新后页面标题不变
        this.title = localStorage.getItem('title')
      }
    }
  }
</script>

<style>
  .cus-nav{
    padding: 0 20px;
    border-bottom: 0;
    background-color: #00a2ed;
    margin-bottom: 0;
    font-size: 18px;
  }
  .cus-nav li a:hover,
  .cus-nav li a:focus,
  .cus-nav li a:visited{
    background-color: transparent;
  }
  .cus-nav li a{
    padding: 10px;
    border: none;
  }
  .cus-nav a span{
    font-size: 12px;
    color: #fff;
  }
  .cus-nav li a.home,
  .cus-nav li a.home:hover{
    color: #fff;
  }
  .cus-nav li a i{
    color: #fff;
  }
  .cus-ctrl{
    position: absolute;
    top: 5px;
    right: 5px;
    background-color: #fff;
    width: 200px;
    border: none;
  }
  .cus-ctrl a{
    color: #000;
  }
  .cus-nav li a i.collect{
    color: yellow;
  }
  .cus-nav li a i.like{
    color: red;
  }

  /* vue的过渡动画 */
  .rotate-enter-active,
  .rotate-leave-active{
    transition: all .5s ease;
  }
  .rotate-enter, .rotate-leave-active {
    transform: rotate(-90deg);
  }

  /* 夜间模式 */
  .night .cus-nav{
    background-color: #222;
  }
  .night .cus-ctrl{
    background-color: #222;
  }
  .night .cus-ctrl a{
    color: #fff;
  }

    /* 媒体查询 */
  @media screen and (min-width: 768px) {
    .cus-nav{
      height: 80px;
      padding: 15px 0;
      font-size: 30px;
    }
    .cus-ctrl{
      width: 320px;
    }
  }
</style>



