import Vue from 'vue'
import VueRouter from 'vue-router'

// 相关路由的组件
import NewsHome from './components/NewsHome.vue'
import NewsSetting from './components/NewsSetting.vue'
import NewsDetail from './components/NewsDetail.vue'
import NewsCollection from './components/NewsCollection.vue'
import UserLogin from './components/UserLogin.vue'
import SystemMessage from './components/SystemMessage.vue'
import AboutMe from './components/AboutMe.vue'
import ShareBox from './components/ShareBox.vue'
import NewsColumn from './components/NewsColumn.vue'

Vue.use(VueRouter)

// 路由路径
const routes = [{
  // 主页
  path: '/',
  component: NewsHome
}, {
  path: '/home',
  component: NewsHome
}, {
  // 设置页
  path: '/setting',
  component: NewsSetting
}, {
  // 文章详情页
  path: '/article/:id',
  name: 'article',
  component: NewsDetail,
  children: [{ // 子路由分享框
    path: 'share',
    name: 'share',
    component: ShareBox
  }]
}, {
  // 收藏文章页
  path: '/collect',
  component: NewsCollection
}, {
  // 登录页
  path: '/login',
  component: UserLogin
}, {
  // 系统消息页
  path: '/message',
  component: SystemMessage
}, {
  // 自我介绍页
  path: '/aboutme',
  component: AboutMe
}, {
  // 专栏日报页
  path: '/column/:id',
  name: 'column',
  component: NewsColumn
}, {
  // 优先匹配别的路由 匹配到这里重定向
  path: '*',
  redirect: '/'
}]

// 路由实例
const router = new VueRouter({
  mode: 'hash', // 在无服务端配合的情况下 不要使用history模式
  routes
})

export default router

// 可以通过router的beforeEach和afterEach来控制 路由跳转前后的状态 比如滚动条滚动 都有3个参数 to from next



