<template>
<div class="cus-home container-fluid">
  <news-picture></news-picture>
  <news-article></news-article>
  <transition name="show">
    <!-- 可以减少渲染次数 非keep-alive每次更新要重新经过生命周期 -->
    <keep-alive>
      <news-sidebar v-if="isMaskShow"></news-sidebar>
    </keep-alive>
  </transition>
  <!-- 动态添加组件 页面每到底部 又添一条 -->
  <news-load-more v-for=" n in num" :key="n" :newsIndex="startDate - n + 1"></news-load-more>
  <div @click="back2top" v-if="num === limit" class="cus-info">
    <p>没资源了，点击回顶部</p>
  </div>
  <div @click="toggleSidebar" v-if="isMaskShow" class="mask"></div>
  <app-loading :isShow="isLoadingShow"></app-loading>
</div>
</template>

<script>

  import { mapState } from 'vuex'

  import NewsPicture from './NewsPicture.vue'
  import NewsSidebar from './NewsSidebar.vue'
  import NewsArticle from './NewsArticle.vue'
  import NewsLoadMore from './NewsLoadMore.vue'
  import AppLoading from './AppLoading.vue'

  export default {
    name: 'NewsHome',
    components: {
      NewsPicture,
      NewsSidebar,
      NewsArticle,
      NewsLoadMore,
      AppLoading
    },
    data () {
      return {
        num: 0,
        startDate: 20170306,
        limit: 4,  // 本地环境 按文件名取资源 资源有限 有服务器就可以不加限制了
        isLoadingShow: true
      }
    },
    methods: {
      toggleSidebar () {
        this.$store.dispatch('toggleSidebar')
      },
      handleScroll () {
        if (this.$route.path === '/' || this.$route.path === '/home') {
          let home = document.querySelector('.cus-home')
          if (home.clientHeight + home.offsetTop - 50 < window.scrollY + document.documentElement.clientHeight) {
            if (this.num < this.limit) {
              this.num++
              if (!this.isMoreShow) { // 只有在可以关闭的时候才关闭这个控制
                this.$store.dispatch('continueLoadMore') // 控制是否继续加载 全局
              }
            }
          }
        }
      },
      back2top () {
        window.scrollTo(0, 0) // 点击后回到顶部 可以用这个做个回到顶部效果
      }
    },
    // mask和sidebar同时出现
    computed: {
      ...mapState({
        isMaskShow: state => state.isMaskShow,
        isMoreShow: state => state.isMoreShow,
        news: state => state.news
      })
    },
    created () { // 生命周期 属性已经绑定 DOM未生成

    },
    mounted () {  // 模板编译挂载之后
      if (this.$route.path === '/') {
        window.addEventListener('scroll', this.handleScroll, false)
      }
      // 仿的loading效果 其实不需要.3秒 挂载后自动取消
      setTimeout(() => {
        this.isLoadingShow = false
      }, 300)
    }
  }
</script>

<style scoped>
  .cus-home{
    padding: 0;
  }
  .cus-info{
    background-color: #ddd;
    padding: 5px 0;
    border-radius: 10px;
    color: #41b883;
    font-size: 20px;
    text-align: center;
  }
  .mask{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 8888;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .6);
  }

  /* vue的过渡动画 */
    .show-enter-active,
    .show-leave-active{
      transition: all .3s ease-out;
    }
    .show-enter, .show-leave-active {
      transform: translateX(-100%);
    }

  /* 夜间模式 */
  .night .cus-info{
    background-color: #222;
  }
</style>



